body {
  margin: 0;
  padding: 0;
  font-family: 'Lato', 'Lucida Grande', 'Lucida Sans Unicode', Tahoma, Sans-Serif,
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.App {
  text-align: center;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 1vmin);
  /*color: white;*/
}

.App-wrapper {
  background-color: white;
  max-width: 40%;
  height: auto;
  padding: 1.5em;
  border-radius: 5px;
  box-shadow: 10px 7px 93px -9px rgba(143,143,143,0.94);
}

.btn {
  padding: 10px;
  border-radius: 5px;
  font-size: 15px;
  color: white;
  margin-left: 5px; 
  border: 0px;
  cursor: pointer;
}

.share {
  float: left;
  width: 5%;
}

.share:hover { /* IE 9 */
  -webkit-transform: scale(1.1,1.1); /* Safari */
  transform: scale(1.1,1.1); /* Standard syntax */
  box-shadow: 10px 7px 93px -9px rgba(143,143,143,0.94);
}

.creator {
  font-size: 13px;
  padding: 10px;
  color: white;
}

.loader {
  color: #b2bec3;
}

#author {
  text-align: right;
}

#new-quote {
  float: right;
  padding: 10px;
}

